<template>
    <div class="page-container">
        <el-aside class="page-nav-left">
            <div class="aside-bar">
                <el-menu default-active="1">
                    <router-link to="/trace/batch">
                        <el-menu-item index="0">
                            <i class="el-icon-position"></i>
                            <span slot="title">溯源管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/trace/certify">
                        <el-menu-item index="1">
                            <i class="el-icon-circle-check"></i>
                            <span slot="title">认证管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/trace/detect">
                        <el-menu-item index="2">
                            <i class="el-icon-set-up"></i>
                            <span slot="title">检测管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/trace/process">
                        <el-menu-item index="3">
                            <i class="el-icon-setting"></i>
                            <span slot="title">加工管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/trace/delivery">
                        <el-menu-item index="4">
                            <i class="el-icon-truck"></i>
                            <span slot="title">配送管理</span>
                        </el-menu-item>
                    </router-link>
                </el-menu>
            </div>
        </el-aside>
        <el-main class="m-l-220 m-t-20 bg-white">
            <div class="flex flex-align-center flex-pack-justify">
                <div class="flex flex-align-center">
                    <el-select class="m-r-15" size="small" v-model="vm.searchType" placeholder="请选择认证类型">
                        <el-option
                            v-for="item in typeList"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                    <el-input size="small" class="m-r-15" style="width:200px;" placeholder="搜索认证产品名称" suffix-icon="el-icon-search" v-model="vm.searchKey"></el-input>
                    <el-button type="primary" size="small" @click="search">搜索</el-button>
                </div>
                <el-button type="primary" size="small" @click="add">添加认证记录</el-button>
            </div>
            <el-row class="m-t-30">
                <el-col :span="24">
                    <el-table :height="tableHeight" :data="dataList" size="small" border stripe style="width: 100%">
                        <el-table-column prop="certifyType" label="认证类型"></el-table-column>
                        <el-table-column prop="product" label="认证产品"></el-table-column>
                        <el-table-column prop="certifyNo" label="证书编号"></el-table-column>
                        <el-table-column prop="certifyOrg" label="颁证机构"> </el-table-column>
                        <el-table-column prop="certifyTime" label="颁证日期"> </el-table-column>
                        <el-table-column prop="certifyValid" label="有效日期"></el-table-column>
                        <el-table-column fixed="right" label="操作" width="140">
                            <template slot-scope="scope">
                                <el-button type="primary" size="mini" icon="el-icon-search" @click="edit(scope.row)"></el-button>
                                <el-button type="danger" size="mini" icon="el-icon-delete" @click="remove(scope.row)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-col>
            </el-row>
            <el-row class="m-t-30">
                <el-col>
                    <el-pagination 
                        class="text-right" 
                        background 
                        layout="total, prev, pager, next" 
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page.sync="vm.currentPage"
                        :page-size="vm.pageSize"
                        :total="vm.pageTotal">
                    </el-pagination>
                </el-col>
            </el-row>
            <el-dialog title="加工记录" :visible.sync="dialogVisible" width="800px" :close-on-click-modal="false">
                <el-form ref="form" size="small" :rules="rules" :model="form" label-width="150px">
                    <el-form-item label="所属地块" prop="plot">
                        <el-input style="width:260px;" v-model="form.plot" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="认证类型" prop="certifyType">
                        <el-select style="width:260px;" v-model="form.certifyType" filterable placeholder="请选择认证类型">
                            <el-option
                                v-for="item in typeList"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="认证产品" prop="product">
                        <el-select style="width:260px;" v-model="form.product" filterable placeholder="请选择产品">
                            <el-option
                                v-for="item in productList"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="证书编号" prop="certifyNo">
                        <el-input style="width:260px;" v-model="form.certifyNo"></el-input>
                    </el-form-item>
                    <el-form-item label="颁证日期" prop="certifyTime">
                        <el-date-picker style="width:260px;" type="date" placeholder="选择日期" v-model="form.certifyTime"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="颁证机构" prop="certifyOrg">
                        <el-input style="width:260px;" v-model="form.certifyOrg"></el-input>
                    </el-form-item>
                    <el-form-item label="有效日期" prop="certifyValid">
                        <el-date-picker style="width:260px;" type="date" placeholder="选择日期" v-model="form.certifyValid"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="证书图片" prop="certifyPic">
                        <el-upload
                            multiple
                            action="https://jsonplaceholder.typicode.com/posts/"
                            :limit="10"
                            :file-list="form.certifyPic">
                            <el-button size="small" type="primary">点击上传</el-button>
                            <div slot="tip">只能上传jpg/png文件，且不超过500kb</div>
                        </el-upload>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button size="small" @click="dialogVisible = false">取消</el-button>
                    <el-button size="small" type="primary" @click="save">保存</el-button>
                </span>
            </el-dialog>
        </el-main>
    </div>
</template>

<script>
export default {
    data() {
        return {
            vm: {
                searchKey: '',
                searchType: '',
                currentPage: 1,
                pageSize: 10,
                pageTotal: 100,
            },
            typeList: [{
                value: '选项1',
                label: ''
            }, {
                value: '选项2',
                label: '双皮奶'
            }, {
                value: '选项3',
                label: '蚵仔煎'
            }, {
                value: '选项4',
                label: '龙须面'
            }, {
                value: '选项5',
                label: '北京烤鸭'
            }],
            dataList: [{
                plot: '东里旺旺葡萄园',
                certifyType: '绿色食品认证',
                product: '阳光玫瑰（东兰）',
                certifyNo: '20190927',
                certifyOrg: 'XXX机构',
                certifyTime: '2019-09-30',
                certifyValid: '2018-06-14',
            }, {
                plot: '东里旺旺葡萄园',
                certifyType: '绿色食品认证',
                product: '阳光玫瑰（东兰）',
                certifyNo: '20190927',
                certifyOrg: 'XXX机构',
                certifyTime: '2019-09-30',
                certifyValid: '2018-06-14',
            }, {
                plot: '东里旺旺葡萄园',
                certifyType: '绿色食品认证',
                product: '阳光玫瑰（东兰）',
                certifyNo: '20190927',
                certifyOrg: 'XXX机构',
                certifyTime: '2019-09-30',
                certifyValid: '2018-06-14',
            }],
            dialogVisible: false,
            form:{
                plot: '东里旺旺葡萄园',
                certifyType: '',
                product: '',
                certifyNo: '',
                certifyTime: '',
                certifyOrg: '',
                certifyValid: '',
                certifyPic: []
            },
            productList:[],
            rules: {
                certifyType: [
                    { required: true, message: '请选择认证类型', trigger: 'change' },
                ],
                product: [
                    { required: true, message: '请选择产品', trigger: 'change' }
                ]
            }
        }
    },
    computed:{
        tableHeight() {
            return document.documentElement.clientHeight - 275;
        }
    },
    methods:{
        search(){

        },
        add(){
            this.dialogVisible = true
        },
        edit(row) {
            this.resetForm()
            this.dialogVisible = true;
            this.form = row
        },
        save(){
            this.$refs['form'].validate((valid) => {
                if (valid) {
                    console.log(this.form)
                    this.dialogVisible = false
                } else {
                    this.$message.error("请检查您的输入！");
                    return false;
                }
            });
        },
        remove(row){
            this.$confirm('是否删除？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$message({
                    type: 'success',
                    message: '删除成功!'
                });
            }).catch(() => {
                          
            });
        },
        resetForm() {
            this.form = {
                plot: '东里旺旺葡萄园',
                certifyType: '',
                product: '',
                certifyNo: '',
                certifyTime: '',
                certifyOrg: '',
                certifyValid: '',
                certifyPic: []
            }
        },
        handleSizeChange(val) {
            console.log(val);
        },
        handleCurrentChange(val) {
            console.log(val);
        }
    }
}
</script>

<style>

</style>


